<template>
    <v-container id="about-body" fluid>
        <vue-particles 
        color="#5ff"
        linesColor="#fff"
        shapeType="star"
      >
      </vue-particles>
        <v-layout>
            <v-flex xs12 md8 offset-md2>
                <v-layout row>
                    <v-flex xs12>
                        <v-layout id="my-title" row wrap class="mt-5 display-1">
                            <div id="name"></div>
                        </v-layout>
                        <div class="separator"><hr /></div>
                    </v-flex>
                </v-layout>
                <v-layout row>
                    <v-flex xs12>
                        <v-layout row wrap class="mt-5 subheading">
                            <div id="bio">
                          Let's just say I'm your DCE (Developer Crush Everyday) *winks*. I love everything technology, a lot of creative things amaze me, from the Raspberry pi to Arduino and all cool stuffs you can do with them.
                          I haven't gotten the chance to play with any of those but I've just been <i><span class="noteit">surf</span>ing</i> the web, reading and watching all the cool things people have made with them, and oh well, for the main time I guess that's <i><span class="noteit">surf</span>icient</i> [*sufficient*]. However, I plan to get my hands dirty with them soon. 
                            <p>*Sigh*, I'm a PHP fanboy (say what you want), and I've spent more of my coding hours with the language. The love for the language even became stronger when I discovered the web artisan's framework -> Laravel. </p>
                            <p>On a low :), It was impossible for me to ignore the Javascript noise around and I just had to take a look at the language, an action mainly inspired by the VueJS framework</p>
                            <p>I play pool billiards anytime I get the chance to do that, very few pool boards around where I live now, I used to have the luxury on University of Ibadan campus</p> 
                            <p>I teach a couple of people how to code and I love to inspire people who are interested in programming to actually keep going, we are all in this together. I've got my own share of the imposter's syndrome but we're good :) </p>

                            <p>I'm a native of Ibadan and I also build native android apps, yeah not that kind of native but you get the point. I do play on words for no reason, I love pun, i drop punchlines both dry and dope ones, I have my days . I bestowed a 'Punstar' title upon myself *sigh*. </p>
                        <!--<div class="mt-3 mb-3 " align-center><img src="@/assets/mysetup.jpg" id="sneekpeek" width="100%"/></div>-->
                        <p>I'm on my to being useful to the whole world.</p>
                        <!--<div class="mt-3 mb-3 " align-center><img src="@/assets/mysetup.jpg" id="sneekpeek" width="100%"/></div>-->
                            </div>
                        </v-layout>
                    </v-flex>
                </v-layout>
                <v-layout row>
                    <v-flex xs12>
                        <v-layout id="skills-tab" row wrap class="mt-5">
                            <div id="skills" class="headline">Skills</div>
                        </v-layout>
                    </v-flex>
                </v-layout>
            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
import TypeIt from 'typeit'

var typeitname

export default {
  data () {
    return {
      typename: typeitname
    }
  },
  mounted () {
    console.log('created cylce')
    typeitname = new TypeIt('#name', {
    })
    .type('Hey, I\'m Ogundiran Al-Ameen.')
    .pause(2000)
    .options({speed: 50})
    .delete()
    .options({speed: 100})
    .type('Welcome to my world!')

    document.getElementById('bio').classList.add('slide-in-bottom')
  },
  updated () {
    console.log('updated cylce')
    this.typename = typeitname
  }
}
</script>


<style scoped>
#about-body {
  cursor: default;
  height: 100%;
  color:  #B7DAED;
  background: #181818; 
}
#my-title{
    
}
#name{
  font-family: 'TandyOld_TV';
  color: #ffff55;
}
#bio{
    font-family: 'IBM-BIOS-2y';
    line-height: 2;
    
}
.noteit{
    color:firebrick;
}
hr {
    height: 5px;
    margin: 20px 0px;
    background: #fff;
    /*background: linear-gradient(to right, gold, rgb(233, 61, 52));*/
    border-width: initial;
    border-style: none;
    border-color: initial;
    border-image: initial;
}
#skills{
    color: #c60;
}

#particles-js{
  position:absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.slide-in-bottom {
	-webkit-animation: slide-in-bottom 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
	        animation: slide-in-bottom 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}


/* ----------------------------------------------
 * Generated by Animista on 2018-2-23 2:53:16
 * w: http://animista.net, t: @cssanimista
 * ---------------------------------------------- */

/**
 * ----------------------------------------
 * animation slide-in-bottom
 * ----------------------------------------
 */
@-webkit-keyframes slide-in-bottom {
  0% {
    -webkit-transform: translateY(1000px);
            transform: translateY(1000px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}
@keyframes slide-in-bottom {
  0% {
    -webkit-transform: translateY(1000px);
            transform: translateY(1000px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

</style>



