<template>
  <v-container id="home-body" fluid>
    <vue-particles 
        color="#5ff"
        linesColor="#fff"
        shapeType="star"
      >
      </vue-particles>
    <v-layout column align-center>
      <div id="avatararea" class="mt-5 mb-1"><img src="@/assets/mentos.jpg" id="dp" /></div>
      <div class="headline mb-1" id="intro-name"> Ogundiran Al-Ameen </div>
      <div id="nick" class="title mb-3"><span class="code-color">&lt;</span> mentos <span class="code-color">&#47&gt;</span></div>
      <div id="role" class="subheading mb-3">&#123;<span class="profession"> Developer | Speaker | Punstar </span> &#125;</div>
    </v-layout>
    <v-layout class="mt-3" row wrap>
      <v-flex id="nav-zone" xs12  sm8 offset-sm2 md4 offset-md4>
        <v-layout row wrap>
          <v-flex xs4 text-xs-center>
            <router-link class="link" to="/about" tag="a"><span class="bracket">[</span>About<span class="bracket">]</span></router-link>
          </v-flex>
          <v-flex xs4 text-xs-center>
            <router-link class="link" to="/projects" tag="a"><span class="bracket">[</span>Projects<span class="bracket">]</span></router-link>
          </v-flex>
          <v-flex xs4 text-xs-center>
            <router-link class="link" to="/contact" tag="a"><span class="bracket">[</span>Contact<span class="bracket">]</span></router-link>
          </v-flex>
        </v-layout>
      </v-flex>
    </v-layout>
  </v-container>
</template>



<script>
export default {
  data () {
    return {
    }
  },
  mounted () {
    document.getElementById('intro-name').classList.add('tracking-in-expand')
  }
}
</script>
<style scoped>
a{
  position: relative;
  z-index: 9999; /*this is to make link elevate about particle js canvas, making it clickable*/
  /*parent elements of a must not have a smaller z-index*/
}
#home-body {
  cursor: default;
  height: 100%; /* so as to cover full height and background color will be of the entire page */

  background: #181818;/* #136a8a; fallback for old browsers */
/*
  background: -webkit-linear-gradient(to right, #267871, #136a8a);
  background: linear-gradient(to right, #267871, #136a8a);
*/
}
#avatararea{
  min-height: 200px;
  z-index: 9999;
}
#dp{
  max-width: 200px;
  margin-top: 5%;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
}
#intro-name{
  font-weight: 400;
  font-family: 'ATI-8by8-2y';
  color: #ffff55; /*#5ff #424242*/
}
.code-color{
  font-family: sans-serif;
  color: #a73737;
  font-weight: bold;
}
#nick{
  font-family: 'ATI-8by8-2y';
  font-weight: 400;
  font-style: normal;
  color: #ff5555;/*#999999; *//*#263238*/
}
.aka{
  font-family: 'Campton-Light';
  font-style: italic;
  color: white;
}
#role{
  color: #a73737;
}
.profession{
  color: #cc6600; /*#0aa #ECEFF1*/
  font-family: 'Amstrad';
  
}
#nav-zone{
  font-family: 'ATI-8by8-2y';
  border: 1px solid #fff;
  background-color: #323232;
}
.link{
  color: #5ff;/*#ff5555; #263238;*/
  text-decoration: none;
  padding: 4px;
}
.link:hover{
  color: #ffff55;/*#a73737;*/
  background-color: #800;
  text-decoration: none;
}

.link:hover > .bracket{
  color: #ff5555;/*#a73737;*/
}

.bracket{
  color:#0aa;
}


#particles-js{
  position:absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}


.tracking-in-expand {
	-webkit-animation: tracking-in-expand 2s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
	        animation: tracking-in-expand 2s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
}

/* ----------------------------------------------
 * Generated by Animista on 2018-2-23 2:57:30
 * w: http://animista.net, t: @cssanimista
 * ---------------------------------------------- */

/**
 * ----------------------------------------
 * animation tracking-in-expand
 * ----------------------------------------
 */
@-webkit-keyframes tracking-in-expand {
  0% {
    letter-spacing: -0.5em;
    opacity: 0;
  }
  40% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
  }
}
@keyframes tracking-in-expand {
  0% {
    letter-spacing: -0.5em;
    opacity: 0;
  }
  40% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
  }
}
</style>



