<template>
  <v-app>
    <v-content>
      <router-view/>
    </v-content>
    <v-footer id="footer" :fixed="fixed" app>
      <div id="footer-text" class="text-xl-center"><p>&copy; {{year}}. Built with <span class="heart">&#10085;</span> by <a target="_blank" href="http://twitter.com/piomentos"><span class="left-pointer">&#9668;&#9668;</span> mentOS</a> <span class="knight">&#9822</span></p></div>
    </v-footer>
  </v-app>
</template>

<script>
var yr = new Date().getFullYear()
export default {
  data () {
    return {
      fixed: false,
      year: yr
    }
  },
  created () {
    let uri = window.location.href.split('?')
    if (uri.length === 2) {
      let vars = uri[1].split('=')
      let page = vars['1']
      this.$router.replace('/' + page)
    }
  },
  name: 'App'
}
</script>
<style scoped>
a{
  position: relative;
  z-index: 9999; /*this is to make link elevate about particle js canvas, making it clickable*/
  /*parent elements of a must not have a smaller z-index*/
}
#footer{
  background-color: #263238;
  color: white;
  margin-bottom: -5px; /*fix for blank white space at the bottom*/
}
#footer-text{
    margin: 0 auto;
    margin-top: 6px;
    font-family: 'Amstrad';
}
#footer a{
  color: #0a0; /*#a73737*/
  padding: 3px;
  text-decoration: none;
}
#footer a:hover{
  background-color: #cfd8dc;
  opacity: 1;
}
.left-pointer{
  color: #a00;
}
#footer a:hover .left-pointer{
  color: #ff5555;
}
.heart{
  color: #FF7043;
  /* other html heart codes - &#9829; &#10084 &#10085*/
}
.knight{
  color: #00BCD4;
}
@font-face {
  font-family: 'Campton-Light';
  src: url('/static/fonts/Campton-Light.otf') format("opentype");
}
@font-face {
  font-family: 'Campton-Bold';
  src: url('/static/fonts/Campton-Bold.otf') format("opentype");
}
@font-face {
  font-family: 'RobotoSlab-Bold';
  src: url('/static/fonts/RobotoSlab-Bold.ttf');
}
@font-face {
  font-family: 'ATI-8by8-2y';
  src: url('/static/fonts/ATI_8x8-2y.ttf');
}
@font-face {
  font-family: 'Amstrad';
  src: url('/static/fonts/AmstradPC1512-2y.ttf');
}
@font-face {
  font-family: 'IBM-BIOS-2y';
  src: url('/static/fonts/IBM_BIOS-2y.ttf');
}
@font-face {
  font-family: 'TandyOld_TV';
  src: url('/static/fonts/Phoenix_BIOS-2y.ttf');
}

/*#dedede #e32929*/

</style>



